
body {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
#app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    min-height: 800px;
}
.container {
    -ms-flex: 1;
        flex: 1;
}
.border-1px {
  border: 1px solid #ddd;
  box-shadow: 0 2px 4px #ddd;
  margin: 16px;
  padding: 16px;
}
.tags {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.tag {
  display: inline-block;
  color: chocolate;
  margin: 0 16px;
}
/* 全局link样式 */
.active {
    border-bottom: 5px solid rgba(124, 241, 139, 0.8);
}
.hover-underline:hover {
    border-bottom: 5px solid rgba(124, 241, 139, 0.8);
}
a {
    transition: all 0.3s;
    text-decoration: none;
}
.createTime {
    color: darkseagreen;
}

/* make the code container scroll */
pre {
    overflow: scroll;
}



